 import Vue from "vue";
 import Router from "vue-router";
 import AppHeader from "./layout/AppHeader";
 import AppFooter from "./layout/AppFooter";
 import Accueil from "./views/Accueil.vue";
 import Bocal from "./views/Bocal.vue";
 import Landing from "./views/Landing.vue";
 import Login from "./views/Login.vue";
 import Register from "./views/Register.vue";
 import Profile from "./views/Profile.vue";

 Vue.use(Router);

 export default new Router({
     linkExactActiveClass: "active",
     routes: [
         {
             path: "/",
             name: "accueil",
             components: {
                 header: AppHeader,
                 default: Accueil,
                 footer: AppFooter
             }
         },
         {
            path: "/bocal",
            name: "bocal",
            components: {
                header: AppHeader,
                default: Bocal,
                footer: AppFooter
            }
        },
         {
             path: "/landing",
             name: "landing",
             components: {
                 header: AppHeader,
                 default: Landing,
                 footer: AppFooter
             }
         },
         {
             path: "/login",
             name: "login",
             components: {
                 header: AppHeader,
                 default: Login,
                 footer: AppFooter
             }
         },
         {
             path: "/register",
             name: "register",
             components: {
                 header: AppHeader,
                 default: Register,
                 footer: AppFooter
             }
         },
         {
             path: "/profile",
             name: "profile",
             components: {
                 header: AppHeader,
                 default: Profile,
                 footer: AppFooter
             }
         }
     ],
     scrollBehavior: to => {
         if (to.hash) {
             return { selector: to.hash };
         } else {
             return { x: 0, y: 0 };
         }
     }
 });